"use client";
import React, { useState } from 'react';

import { UserQuery } from '../../components/types';
import Header from '@/components/layout/Headder';
import QueryStatusAndCommunication from '@/components/QueryStatusAndCommunication';
import Footer from '@/components/layout/Footer';

const API_BASE = "http://localhost:5000/api"; // Adjust as needed

const HistoryPage = () => {
  const [userEmail, setUserEmail] = useState("");
  const [userQueries, setUserQueries] = useState<UserQuery[]>([]);
  // eslint-disable-next-line @typescript-eslint/no-unused-vars
  const [cartItemCount, setCartItemCount] = useState(0); // Keep dummy or fetch from elsewhere if needed

  const fetchUserQueries = async () => {
    if (!userEmail) {
      alert("Please enter your email");
      return;
    }
    try {
      const res = await fetch(`${API_BASE}/user-queries/${encodeURIComponent(userEmail)}`);
      if (!res.ok) throw new Error("Failed to fetch queries");
      const data = await res.json();
      setUserQueries(data);
    } catch (error) {
      alert("Error fetching your queries. Try again later.");
      console.error(error);
    }
  };

  // For testing only: simulate doctor reply
  const handleSimulateDoctorReply = (queryId: string, replyMessage: string) => {
    setUserQueries(prevQueries =>
      prevQueries.map(query =>
        query.id === queryId
          ? { ...query, status: 'Doctor Replied (Simulated)', doctorReply: replyMessage }
          : query
      )
    );
    alert("Doctor's reply simulated!");
  };

  return (
    <div className="bg-slate-100 min-h-screen flex flex-col">
      <Header cartItemCount={cartItemCount} onCartClick={() => alert("Please go to the home page to manage cart.")} /> 
      
      <main className="flex-grow py-8 container mx-auto px-4">
        <div className="mb-6">
          <input
            type="email"
            placeholder="Enter your email to load history"
            className="border p-2 rounded w-full max-w-md"
            value={userEmail}
            onChange={e => setUserEmail(e.target.value)}
          />
          <button
            className="ml-4 px-4 py-2 bg-blue-600 text-white rounded"
            onClick={fetchUserQueries}
          >
            Load History
          </button>
        </div>

        <QueryStatusAndCommunication
          userQueries={userQueries}
          onSimulateDoctorReply={handleSimulateDoctorReply}
        />
      </main>

      <Footer />
    </div>
  );
};

export default HistoryPage;
